.special-content h5 {
  margin-bottom: 15px;
}
/* end of banner background */

/* product aside  */
.product-aside {
  margin-bottom: 20px;
}
.product-aside ul li {
  line-height: 30px;
  display: block;
  margin-bottom: 12px;
  text-transform: uppercase;
  color: #000;
  font: 500 18px "Roboto Slab";
}
.product-aside h3:after {
  position: absolute;
  content: "";
  bottom: -5px;
  left: 0;
  width: 70px;
  height: 2px;
  background: #80b435;
}
.product-aside ul li {
  line-height: 30px;
  display: block;
  /* position: relative; */
  margin-bottom: 12px;
  /* padding-bottom: 0px; */
  /* padding-top: 0px; */
  text-transform: uppercase;
  color: #000;
  font: 500 18px "Roboto Slab";
  cursor: pointer;
}
.product-aside-title {
  color: #333;
  text-transform: uppercase;
  font-style: normal;
  padding-bottom: 10px;
  margin-bottom: 20px;
  position: relative;
  font: 700 24px "Roboto Slab";
}
.container-modified {
  width: 100%;
  padding: 0 30px;
}
.product-banner {
  padding: 50px 0;
  min-height: 200px;
  background-size: 100%;
  background-image: url("./images/productsbanner.jpg");
  background-repeat: no-repeat;
}
.product-banner-inner {
  text-align: center;
  color: #fff;
  padding: 35px;
}
.product-banner-inner h3 {
  display: inline-block;
  width: 100%;
  text-transform: uppercase;
  font-size: 65px;
  font-family: "Roboto Slab";
  line-height: 35px;
  letter-spacing: 5px;
  padding-bottom: 15px;
}
.order-left svg {
  border: 1px solid #cdcdcd;
  padding: 5px;
  font-size: 35px;
  margin-right: 6px;
}
.ordering {
  margin-bottom: 20px;
  display: flex;
}
.order-left {
  display: flex;
}
.order-left p {
  line-height: 31px;
  margin-left: 20px;
  color: #2b2b2b;
  font-size: 16px;
  font-weight: 500;
}

/* end of product aside */
/* list view product */
.listview-wrap {
  display: flex;
  padding: 20px 0 20px 10px;
  margin-bottom: 15px;
  transition: all 0.3s;
}
.listview-wrap:hover {
  box-shadow: 0px 8px 33px 0px rgba(0, 0, 0, 0.13);
  z-index: 2;
}
/* .listview-wrap:hover .listview-img {
  transform: scale(1.5);
} */
.listview-img {
  width: 30%;
  margin-right: 10%;
  overflow: hidden;
}
/* .listview-img img {
  transition: transform 0.2s;
} */
.listview-name {
  font-size: 35px;
  color: #000;
  margin-bottom: 5px;
}
.listview-price {
  font-size: 22px;
  color: #80b435;
  font-weight: 600;
  margin-bottom: 5px;
}
.listview-description {
  font: 400 19px/30px "Roboto Slab";
  color: #666;
}

/* end of list view product */

/* Dashboard section  */
.admin-dashboard-section {
  overflow: hidden;
}
.dashboard-header {
  text-align: center;
  margin: 15px 0 25px;
  color: #80b435;
}
.admin-navbar {
  background: #a9a9a9;
  height: 100%;
  position: fixed;
  width: 200px;
}

.admin-navbar li a {
  padding: 5px 10px;
  display: block;
  color: #fff;
  text-transform: capitalize;
}
.admin-navbar .logo {
  padding: 15px 0 0;
  margin-bottom: 25px;
}
/* .admin-dashboard-section {
} */

/* order section  */
.order-list {
  background: #f3f3f3;
  margin-bottom: 15px;
  padding: 20px;
}
.order-list p {
  font-size: 18px;
  font-weight: 500;
}
.order-address-info {
  display: flex;
  justify-content: space-around;
}
.order-item-list {
  border-bottom: 2px solid #fff;
  display: flex;
  justify-content: space-between;
  width: 90%;
  align-items: center;
  margin-bottom: 10px;
}
.order-item-list img {
  width: 130px;
}
.order-list h5 {
  text-transform: uppercase;
  color: #80b435;
}
/* end of order section  */
